<template>
    <div class="item">
        <div class="title" :class="{'title-hightlight': check==true && is_true != -1 && is_true!=select}">
            <h3>Câu {{ index + 1 }}. {{ item.question }}</h3>
            <img v-show="check==true && is_true != -1 && is_true!=select" class="close" src="data:image/png;base64,">
            <img v-show="check==true && is_true != -1 && is_true==select" class="tick" src="data:image/png;base64,">
        </div>
        <div class="item-answer">
            <p @click="onSelect(0)" :class="{p: select==0, 'p-hightlight': check==true && is_true != -1 && is_true!=select && is_true==0 }">A. {{ item.answers[0].answer }}</p>
            <p @click="onSelect(1)" :class="{p: select==1, 'p-hightlight': check==true && is_true != -1 && is_true!=select && is_true==1 }">B. {{ item.answers[1].answer }}</p>
            <p @click="onSelect(2)" :class="{p: select==2, 'p-hightlight': check==true && is_true != -1 && is_true!=select && is_true==2 }">C. {{ item.answers[2].answer }}</p>
            <p @click="onSelect(3)" :class="{p: select==3, 'p-hightlight': check==true && is_true != -1 && is_true!=select && is_true==3 }">D. {{ item.answers[3].answer }}</p>
        </div>
    </div>
</template>
  
<script>
export default {
    name: 'Item',
    props: ['item', 'index', 'check'],
    data(){
        return {
            select: -1,
            is_true: -1
        }
    },
    methods: {
        onSelect(i){
            this.select = i
        }
    },
    watch: {
        check: function(){
            if(this.check == true){
                this.item.answers.forEach((a, i) => {
                    if(a.status == true) this.is_true = i
                })
            } else {
                this.select = -1
                this.is_true = -1
            }
        }
    }
}
</script>
  
