import { createRouter, createWebHistory } from "vue-router";
import Home from '@/views/home'
import Contact from '@/views/contact'

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/contact',
        name: 'Contact',
        component: Contact
    }
]

const router = createRouter({
    history: createWebHistory('/vocatoeic/'),
    routes
})

export default router