import { createStore } from "vuex";
import list from '@/assets/Vocabulary'



const store = createStore({
    state: {
        nemberQuestion: 30,
        listQuestion: []
    },
    mutations: {
        createQuestions(state){
            var arrayIndex = []
            while(arrayIndex.length < state.nemberQuestion){
                arrayIndex.push(Math.floor(Math.random() * list.length))
                arrayIndex = [...arrayIndex]
            }
            var arrayQuestion = []
            arrayIndex.forEach(i => {
                arrayQuestion.push({
                    question: list[i].question,
                    answers: [
                        {
                            answer: list[i].answer,
                            status: true
                        }
                    ]
                })
            })
            arrayQuestion.forEach(item => {
                var answers = []
                while(answers.length < 3){
                    let i = Math.floor(Math.random() * list.length)
                    if(list[i].answer != item.answers[0].answer){
                        answers.push(i)
                    }
                    answers = [...answers]
                }
                var answers2 = []
                answers.forEach(i => {
                    answers2.push({
                        answer: list[i].answer,
                        status: false
                    })
                })
                item.answers = item.answers.concat(answers2)
            })
            arrayQuestion.forEach(item => {
                let i = Math.floor(Math.random() * 4)
                let temp = item.answers[i]
                item.answers[i] = item.answers[0]
                item.answers[0] = temp
            })
            state.listQuestion = arrayQuestion
        }
    }
})

export default store